  margin-top: auto;
  margin-left: 15px;
}
.weui-btn-area {
  margin: 1.17647059em 15px 0.3em;
}
.weui-btn-area_inline {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
